@font-face{
  font-family: Droid serif;
  src: url(/static/media/DroidSerif-Regular.bfb2f44a.ttf);
}

body {
  margin: 0;
  padding: 0;
  background-color: #dddddd;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  margin-top: 50px;
  text-align: center;
  width: 60%;
  border: black 1px solid;
  margin-left: auto;
  margin-right: auto;
  background-color: #f0f0f0;
  font-family: "Droid Serif";
}
@media screen and (max-width: 800px){
  .app {
    width:99%;
    margin-top: 0;
  }
}
.title-header h1{
    width: 350px;
    margin-left: auto;
    margin-right: auto;
    font-size: 45px;
}
@media screen and (max-width: 800px){
    .title-header h1{
        margin-top: 50px;
        margin-bottom: 45px;
        width: 100%;
        font-size: 50px;
    }
}
.add-stock-div {
    margin-bottom: 20px;
}
.stock-input {
    padding: 0 0 0 8px;
    height: 40px;
    width: 300px;
    border: #a9a9b9 1px solid;
    color: black;
    border-radius: 3px;
    font-size: 15px;
    vertical-align: top;
}
.add-button{
    font-weight: 500;
    height: 42px;
    width: 110px;
    margin-left: 10px;
    border-radius: 3px;
    color: white;
    background-color: #73ad21;
    border: #73ad21;
    vertical-align: top;
}
.search-list{
    list-style: none;
    width: 308px;
    padding: 0;
    background-color: white;
    color:black;
    position: absolute;
    left: 46.9%;
    top: 205px;
    margin: 0;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    border-radius: 3px;
}
.search-list li {
    cursor: pointer;
    display: flex;
    padding: 1px 0 2px 0;
    font-size: 15px;
    justify-content: space-between
}
.search-list li:hover{
    background-color: darkgray;
}
.search-list li span {
    padding-left: 5px;
    padding-right: 5px;
}
.error-pop-up{
    color:black;
    z-index: 100;
    background-color: #a9a9b9;
    position: absolute;
    width: 250px;
    height: 60px;
    border: red 1px solid;
    top: 77px;
    left: 47%;
    padding: 5px;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    border-radius: 3px;
}
.error { 
    border: 1px solid red; 
}
@media screen and (max-width: 800px){
    .stock-input {
      width: 64%;
      height: 60px;
      font-size: 17px;
    }
    .add-button{
        height: 62px;
        width: 100px;
        font-size: 16px;
    }
    .add-stock-div {
        margin-bottom: 40px;
    }
    .search-list{
        width: 300px;
        left: 38%;
        top: 214px;
    }
    .error-pop-up{
        top: 67px;
    }
}
.stock-list{
    margin-bottom: 40px;
}
.no-stocks-header{
    height: 400px;
    margin-top: 220px;
    color: #a9a9b9;
}
.stock-card{
    width: 370px;
    height: 120px;
    background-color: white;
    display: inline-block;
    margin: 10px;
    border: #a9a9b9 1px solid;
    border-radius: 3px
}
.stock-card-top{
    display: flex;
    justify-content: space-between;
}
.stock-card-top-left{
    text-align: start;
}
.stock-high{
    margin: 10px;
    font-size: 16px;
}
.stock-name-header{
    font-size: 17px;
    margin: 7px 0 1px 10px;
}
.stock-symbol{
    margin: 0 0 0 11px;
    font-size: 14px;
    color: #8b8ba0;
}
.stock-card-bottom{
    display: flex;
    justify-content: space-between;
}
.stock-card-bottom-left{
    text-align: start;
    margin: 23px 0 0 12px;
}
.change-amount{
    color: red;
    font-size: 13px;
}
.stock-price{
    margin: 0;
    font-weight: 500;
    font-size: 24px;
}
.stock-low{
    margin: 48px 10px 0 0;
    font-size: 16px;
}
.pos{
    color: green;
}
@media screen and (max-width: 800px){
    .stock-card {
      width: 90%;
      height: 68px;
      margin: 0 0 8px 0;
    }
    .stock-card-top{
        float: left;
    }
    .stock-card-bottom{
        float: right;
        margin: 5px 10px 0 0;
    }
    .stock-card-bottom-left{
        margin: 5px 0 0 12px;
        text-align: end;
    }
    .stock-price{
        margin: -3px 0 0 30px;
    }
    .change-amount{
        font-size: 16px;
        font-weight: 550;
    }
    .stock-name-header{
        font-size: 19px;
        margin: 12px 0 1px 12px;
    }
    .stock-symbol{
        font-size: 16px;
        margin: 0 0 0 13px;
    }
    .stock-high {
        display: none;
    }
    .stock-low {
        display: none;
    }
}
